import React, { useRef } from 'react';
import { motion } from 'framer-motion';
import Button from '../components/Button';
import { useTheme } from '../contexts/ThemeContext';
import { colors, typography } from '../styles/sharedStyles';
import videoSrc from '../assets/video3.webm'; // Import the video

function About() {
  const { darkMode } = useTheme();
  const videoRef = useRef(null);

  return (
    <div className={`${colors.background} ${typography.sans} ${darkMode ? 'dark' : ''} min-h-screen`}>
      <header>
        <meta charSet="UTF-8" />
        <meta name="viewport" content="width=device-width, initial-scale=1.0" />
        <title>Sobre Nosotros - Bufete Legal Costa Rica</title>
      </header>

      <main className="pt-20"> {/* Add padding-top to create space for the header */}
        <section className={`${colors.primary} text-white py-16`}>
          <div className="container mx-auto px-4">
            <motion.h1
              className="text-4xl font-bold mb-4 text-center"
              initial={{ opacity: 0, y: -20 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.5 }}
            >
              Sobre Bufete Legal Costa Rica
            </motion.h1>
            <motion.p
              className="text-xl text-center"
              initial={{ opacity: 0, y: -20 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.5, delay: 0.2 }}
            >
              Excelencia y compromiso en servicios legales
            </motion.p>
          </div>
        </section>

        <section className="py-16">
          <div className="container mx-auto px-4">
            <div className="grid grid-cols-1 md:grid-cols-2 gap-8 items-center mb-16">
              <motion.div
                initial={{ opacity: 0, x: -20 }}
                animate={{ opacity: 1, x: 0 }}
                transition={{ duration: 0.5 }}
              >
                <img src="/api/placeholder/400/400" alt="Bufete Legal Costa Rica" className="rounded-lg mx-auto w-full max-w-md h-auto object-cover shadow-lg" />
              </motion.div>
              <motion.div
                initial={{ opacity: 0, x: 20 }}
                animate={{ opacity: 1, x: 0 }}
                transition={{ duration: 0.5, delay: 0.2 }}
              >
                <h2 className="text-3xl font-bold mb-4 dark:text-white">Nuestra Firma</h2>
                <p className={`${colors.text} dark:text-gray-300 mb-4`}>
                  Bufete Legal Costa Rica es una firma de abogados líder, comprometida con la excelencia en la prestación de servicios legales. Nos especializamos en diversas áreas del derecho, incluyendo derecho corporativo, propiedad intelectual, derecho laboral y derecho ambiental.
                </p>
                <p className={`${colors.text} dark:text-gray-300 mb-4`}>
                  Con un equipo de profesionales altamente calificados y una trayectoria de éxito, nos enorgullecemos de ofrecer soluciones legales innovadoras y personalizadas para nuestros clientes.
                </p>
              </motion.div>
            </div>

            <motion.div
              className="bg-white dark:bg-gray-800 rounded-lg shadow-lg p-8 mb-16"
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.5, delay: 0.4 }}
            >
              <h2 className="text-2xl font-semibold mb-4 dark:text-white">Nuestros Valores</h2>
              <ul className="list-disc list-inside space-y-2 dark:text-gray-300">
                <li>Integridad y ética profesional en todas nuestras acciones</li>
                <li>Compromiso con la excelencia y la innovación legal</li>
                <li>Enfoque centrado en el cliente y atención personalizada</li>
                <li>Responsabilidad social y contribución a la comunidad</li>
                <li>Desarrollo continuo y adaptación a las nuevas tecnologías</li>
              </ul>
            </motion.div>

            <motion.div
              className={`${colors.secondary} text-white p-8 rounded-lg mb-16`}
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.5, delay: 0.6 }}
            >
              <h2 className="text-2xl font-semibold mb-4">Nuestro Enfoque</h2>
              <ul className="list-disc list-inside space-y-2">
                <li>Asesoría legal integral y estratégica</li>
                <li>Soluciones personalizadas para cada cliente</li>
                <li>Uso de tecnología avanzada para mejorar nuestros servicios</li>
                <li>Colaboración con expertos en diversas áreas del derecho</li>
                <li>Compromiso con la formación continua de nuestro equipo</li>
              </ul>
            </motion.div>
          </div>
        </section>

        <section className="relative py-32 min-h-screen overflow-hidden">
          <div className="absolute inset-0">
            <video
              ref={videoRef}
              autoPlay
              loop
              muted
              playsInline
              className="w-full h-full object-cover"
            >
              <source src={videoSrc} type="video/webm" />
              Your browser does not support the video tag.
            </video>
            <div className="absolute inset-0 bg-black opacity-60"></div>
          </div>

          <div className="relative z-10 container mx-auto px-4 flex items-center justify-center min-h-screen">
            <motion.div
              className="max-w-4xl mx-auto text-center text-white"
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.5 }}
            >
              <h2 className="text-3xl md:text-4xl font-bold mb-6">Experiencia y Visión</h2>
              <p className="text-lg md:text-xl mb-8">
                Con más de 15 años de experiencia en el campo legal, nuestro equipo ha liderado casos complejos y transacciones internacionales, siempre a la vanguardia de las nuevas tecnologías y tendencias legales.
              </p>
              <p className="text-lg md:text-xl mb-8">
                Nuestro enfoque innovador y compromiso con la excelencia nos han convertido en un referente en derecho corporativo y tecnológico en Costa Rica y más allá.
              </p>
              <Button to="/contacto" variant="secondary" className="text-lg px-8 py-3">
                Solicite una Consulta
              </Button>
            </motion.div>
          </div>
        </section>

        <section className="py-16 bg-gray-100 dark:bg-gray-900">
          <div className="container mx-auto px-4 text-center">
            <h2 className="text-3xl font-bold mb-8 dark:text-white">Compromiso con la Comunidad</h2>
            <p className="text-xl mb-8 dark:text-gray-300">
              Roger lidera un programa de mentoría para estudiantes de derecho de comunidades subrepresentadas, buscando fomentar una mayor diversidad en el campo legal costarricense.
            </p>
            <Button to="/contacto" variant="primary">Agendar Consulta con Roger</Button>
          </div>
        </section>
      </main>
    </div>
  );
}

export default About;